
.Image{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #002b55;
    color: #e7e7e7;
    height: 92vh;
}

.waldo{
    aspect-ratio: 16,9;
    max-height: 78vh;
    background-color: rgb(240, 79, 79);
    cursor: url("./../../images/select_64.png") 32 32, auto;
}

.findChar{
    display: flex;
    justify-content: center;
    align-items: center;
}

.findChar--container{
    display: flex;
    margin: 5px;
    justify-content: space-around;
    align-items: center;
    gap: 3vw;
}

.winMenu{
    display: flex;
    justify-content: space-around;
    gap: 3vw;
    margin-bottom: 10px;
}

.scoreForm{
    position: absolute;
    top: 50%; right: 50%;
    transform: translate(50%,-50%);
    width: 320px;
    height: 250px;
    color: #ffffff;
    background-color: #002b55;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 15px;
    gap: 5px;
    box-shadow: 0px 0px 10px 10px white;

}

.scoreForm > h2{
    font-size: 2em;
}

form{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5px;
}

#name{
    font-size: 1.1em;
}

input{
    text-align: center;
}

.quit{
    position: absolute;
    right: 0;
    top: 0;
    width: 20px;
    height: 20px;
    transform: translate(-10px,10px);
    background-color: red;
    border-style: none;
    border-radius: 10px;
    color: white;
    cursor:pointer;
}

form > button, .checkLead{
    margin: 5px;
    min-width: 40%;
    min-height: 4vh;
    font-family: 'Inter';
    font-size: 1.1em;
    background-color: rgb(71, 196, 71);
    border-style: none;
    border-radius: 15px;
    cursor: pointer;
}