.leaderboards{
    height: 92vh;
    background-color: #002b55;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    color: #e7e7e7;
}

.leaderboards--title{
    margin: 2vh;
}

.leaderboards--grid{
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-around;
    flex-wrap: wrap-reverse;
    gap: 2vh;
}

.leaderboards--info{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    height: 21vh;
    margin: 2vw;
}

.leaderboard--char--container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.leaderboards--map{
    display: flex;
    align-items: center;
    justify-content: center;
}

.leaderboard--img{
    height: 20vh;
}

.leaderboard--char{
    max-width: 150px;
}

.leaderboard--char > img{
    width: 30%;
}