.lMap{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #002b55;
    height: 92vh;
    color: white;
    gap: 10px;
}


.score--container{
    width: 40vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #62bef0;
    padding: 20px;
    border-style:solid;
    border-width: 5px;
    border-color: rgb(218, 218, 218);
    margin-bottom: 10px;
}

.score--container:nth-child(2){
    background-color: black;
    border-color: white;
}



.score--name{
    text-align: left;
    font-size: larger;
    font-weight: 900;
    letter-spacing: 1px;
    text-transform: uppercase;
}

.score--time{
    font-size: larger;
    font-weight: 900;
    letter-spacing: 1px;
    text-transform: uppercase;
}